<template>
  <div class="cmp-share">
    <div class="share-link-container">
      <p class="msg-thanks">Thanks for playing!</p>
      <p
        class="msg-info"
      >Share the link below with your friends and find out how well they know you.</p>
      <p class="share-link-code has-background-light" disabled="{true}">
        {{shareLink}}
        <span
          @click="copyToClipboard(shareLink)"
          class="copy-link-button"
        >{{copyLinkText}}</span>
      </p>
      <div class="social-share">
        <div class="whatsapp">
          <a
            class="button is-primary"
            href="whatsapp://send?text=${sL}"
            data-action="share/whatsapp/share"
          >Share in Whatsapp</a>
        </div>
        <div class="fb">
          <a class="button is-info" href="fb-messenger://share/?link=${sL}">Share in messenger</a>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  data() {
    return {
      copyLinkText: "copy"
    };
  },
  computed: {
    ...mapState(["authorInfo"]),
    shareLink() {
      return `${window.location.href}/${this.authorInfo.key}`;
    }
  },
  methods: {
    copyToClipboard(text) {
      let _this = this;
      var dummy = document.createElement("textarea");
      // to avoid breaking orgain page when copying more words
      // cant copy when adding below this code
      // dummy.style.display = 'none'
      document.body.appendChild(dummy);
      //Be careful if you use texarea. setAttribute('value', value), which works with "input" does not work with "textarea".  Eduard
      dummy.value = text;
      dummy.select();
      document.execCommand("copy");
      document.body.removeChild(dummy);

      _this.copyLinkText = "copied";
      setTimeout(function() {
        _this.copyLinkText = "Copy";
      }, 5000);
    }
  }
};
</script>

