export const changeSentenceBasedOnGender = (question, name, gender) => {
  var mapObj = {
    "%friendName%": name,
    "his/her": gender === "m" ? "his" : "her",
    "he/she": gender === "m" ? "he" : "she",
    "himself/herself": gender === "m" ? "himself" : "herself",
  };
  return question.replace(
    /%friendName%|his\/her|he\/she|himself\/herself/gi,
    function(matched) {
      return mapObj[matched];
    }
  );
};

export const compareObj = (obj_1, obj_2) => {
  let answered = 0;
  for (let key in obj_1) {
    if (obj_1[key] === obj_2[key]) {
      answered++;
    }
  }
  return answered;
};
