<template>
  <div class="cmp-result">
    <table class="table">
      <thead>
        <tr>
          <th>Name</th>
          <th>Score</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(score, index) in result" :key="index">
          <td>{{score.name}}</td>
          <td>{{score.answered}}</td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import { mapState } from "vuex";
import fbConfig from "@/config/firebase_config";
export default {
  data() {
    return {
      result: []
    };
  },
  mounted() {
    let _this = this;
    let score = [];
    fbConfig
      .database()
      .ref(`/${_this.authorInfo.key}/friendData`)
      .once("value", response => {
        let allResult = response.val();

        for (let key in allResult) {
          score.push({
            name: allResult[key].name,
            answered: allResult[key].answered
          });
        }
        _this.result = [...score];
      });
  },
  computed: {
    ...mapState(["authorInfo", "friendInfo"])
  }
};
</script>

<style scoped>
.cmp-result {
  padding: 4%;
}
.cmp-result table {
  width: 100%;
}
</style>