<template>
  <div class="cmp-question">
    <p class="question">{{currentQuestion.question}}</p>
    <div
      class="q-option-container is-flex"
      :class="{'disable-click': selectedOptionIndex !== null}"
    >
      <div
        v-for="(option,index) in currentQuestion.options"
        :key="index"
        class="q-option"
        :class="{'selected-option': selectedOptionIndex === index}"
      >
        <Option @click="handleAnswerClick(currentQuestion.id,option,index)">{{option}}</Option>
      </div>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import { mapActions, mapState } from "vuex";
import Option from "@/components/Option.vue";
import Questions from "@/data";
import firebaseConfig from "@/config/firebase_config";
import {compareObj} from "@/helper";
export default {
  name: "Question",
  data() {
    return {
      currentQuestion: {},
      currentQuestionIndex: 0,
      answers: {},
      selectedOptionIndex: null
    };
  },
  mounted() {
    this.loadQustion(this.currentQuestionIndex);
  },
  computed: {
    ...mapState([
      "authorInfo",
      "authorAnswers",
      "friendAnswers",
      "friendInfo",
      "isFriend"
    ])
  },
  methods: {
    ...mapActions([
      "saveAuthorInfo",
      "saveAuthorAnswers",
      "saveFriendInfo",
      "saveFriendAnswers"
    ]),
    loadQustion(questionIndex) {
      let _this = this;
      _this.selectedOptionIndex = null;
      _this.currentQuestion = { ...Questions[questionIndex] };
    },
    handleAnswerClick(questionID, selectedOption, optionIndex) {
      let _this = this;
      _this.selectedOptionIndex = optionIndex;
      _this.answers[questionID] = selectedOption;
      setTimeout(() => {
        if (_this.currentQuestionIndex < Questions.length - 1) {
          _this.currentQuestionIndex++;
          _this.loadQustion(_this.currentQuestionIndex);
          console.log("currentQuestionIndex", _this.currentQuestionIndex);
        } else {
          if (_this.isFriend) {
            _this.saveFriendAnswers(_this.answers);
            let friendKey = firebaseConfig
              .database()
              .ref(`/${_this.authorInfo.key}/friendData`)
              .push({
                answered: compareObj(_this.authorAnswers, _this.friendAnswers),
                name: _this.friendInfo.name,
                gender: _this.friendInfo.gender
              });
            friendKey.then(response => {
              _this.saveFriendInfo({ key: response.key });
              _this.$router.push({ path: "/result" });
            });

            return;
          }
          let authorKey = firebaseConfig
            .database()
            .ref("/")
            .push({
              authorData: {
                authorAnswers: _this.answers,
                name: _this.authorInfo.name,
                gender: _this.authorInfo.gender
              }
            });

          authorKey.then(response => {
            _this.saveAuthorInfo({ key: response.key });
            _this.saveAuthorAnswers(_this.answers);
            _this.$router.push({ path: "./share" });
          });
        }
      }, 500);
    }
  },
  components: {
    Option
  }
};
</script>

